<template>
	<view>
		<view class="search-area">
			<uni-search-bar @confirm="search" :focus="true" v-model="searchValue" placeholder='搜索关键词'
				cancelButton='none' @blur="blur" @focus="focus" @cancel="cancel" @input="input">
			</uni-search-bar>
			<view class="search-right" @click="search">搜索</view>
		</view>
		<view class="search-auto" v-show="autoShow && autoDatas.length > 0">
			<view class="border" v-for="(item,index) in autoSearch" :key="item.id" @click="search()">
				<img class='pic' src="https://xcxcs.obs.cn-east-3.myhuaweicloud.com/images/search2.png" alt="">
				<text class="txt">{{item.keyword}}</text>
			</view>
		</view>
	</view>
</template>

<script>
	export default {
		name: 'search-area',
		props: {
			autoDatas: {
				type: Array,
				default: []
			}
		},
		options: {
			styleIsolation: 'shared'
		},
		data() {
			return {
				historyDatas: [],
				hotDatas: [],
				autoShow: false,
				autoSearch: [],
				searchValue: '',
				historyNum:1
			}
		},
		methods: {
			checkInput() {
				var that = this;
				let str = that.searchValue.replace(/\s+/g, '');
				that.searchValue = str;
				if (str == '') {
					return true;
				} else {
					return false;
				}
			},
			search(res) {
				var that = this;
				if (this.checkInput()) {
					uni.showToast({
						title: '请输入搜索内容!',
						icon: 'none'
					})
				} else {
					uni.getStorage({
						key:'historyNum',
						success:res=>{
							that.historyNum=res.data;
						}
					});
					that.historyNum++;
					var obj = new Object({
						id: that.historyNum,
						keyword: that.searchValue
					});
					//#ifdef MP-WEIXIN
					console.log(obj);
					//#endif
					uni.getStorage({
						key: 'historyDatas',
						success:(res) =>{
							that.historyDatas = res.data;
						}
					});
					that.historyDatas.unshift(obj);
					// console.log(that.historyNum);
					that.checkRepeat(that.historyDatas);
					console.log(that.historyDatas);
					if (this.historyDatas.length > 10) {
						that.historyDatas.pop();
					};
					this.setcache();
					uni.navigateTo({
						url: 'searchDetail'
					});
				}
				// this.$emit("history", this.historyDatas);

			},
			searchHistory(val) {
				uni.navigateTo({
					url: 'searchDetail'
				});
			},
			checkRepeat(arr) {
				for (let i = 0; i < arr.length; i++) {
					for (let n = i + 1; n < arr.length; n++) {
						if (arr[i].keyword === arr[n].keyword) {
							arr.splice(n, 1);
							n--;
						}
					}
				}
			},
			autoComplete(res) {
				var that = this;
				let arr = [];
				for (let i = 0; i < that.autoDatas.length; i++) {
					var str = that.autoDatas[i].keyword;
					if (str.indexOf(res) != -1) {
						arr.push(that.autoDatas[i]);
					}
				}
				that.autoSearch = arr;
			},
			focus(res) {},
			blur(res) {
				this.searchValue = res.value;
				this.autoShow = false;
			},
			input(res) {
				var that=this;
				if (res != '') {
					that.searchValue = res;
					that.autoShow = true;
					that.autoComplete(res);
					// console.log(that.autoSearch)
				};
			},
			cancel(res) {
				this.searchValue = res.value;
				this.autoShow = false;
			},
			setcache() {
				var that = this;
				uni.setStorage({
					key: 'historyDatas',
					data: that.historyDatas,
					success: function() {
						// console.log(that.historyDatas);
					}
				});
				uni.setStorage({
					key: 'historyNum',
					data: that.historyNum,
					success: function() {
						// console.log(that.historyNum);
					}
				});
			},
		},
		mounted() {
			// console.log(this.autoDatas)
		},
		watch: {
			autoDatas: function(newVal, oldVal) {
				// console.log(newVal)
			},
			historyNum:function(newVal, oldVal) {
				console.log(newVal)
			},
		},


	}
</script>

<style lang="scss">
	.search-area {
		display: flex;
		flex-wrap: nowrap;
		justify-content: space-between;
		align-items: center;
		padding: 5rpx 30rpx 35rpx;

		.search-right {
			font-size: 28rpx;
			color: #0072FE;
		}
	}

	::v-deep .uni-searchbar {
		width: 620rpx;
		height: 68rpx;
		padding: 0 !important;
	}

	::v-deep .uni-searchbar__box {
		border-radius: 34rpx !important;
		width: 620rpx;
		height: 68rpx !important;
	}

	::v-deep .uniui-search:before {
		content: '';
		background: url(https://xcxcs.obs.cn-east-3.myhuaweicloud.com/images/search.png) no-repeat;
		background-size: 100%;
		width: 40rpx;
		height: 40rpx;
		display: block;
		margin: 0 10rpx 0 30rpx;
	}

	::v-deep .uni-searchbar__box-icon-search {
		padding: 0% !important;
	}

	.search-auto {
		padding: 0 30rpx;
		position: absolute;
		top: 108rpx;
		left: 0;
		right: 0;
		z-index: 10;
		background-color: #fff;

		.border {
			display: flex;
			flex-wrap: nowrap;
			justify-content: left;
			align-items: center;
			padding: 30rpx 0;
			border-bottom: 1px solid #D9D9D8;

			.pic {
				width: 24rpx;
				height: 23rpx;
				margin-right: 20rpx;
			}

			.txt {
				font-size: 26rpx;
				line-height: 30rpx;
				color: #020202;
			}
		}
	}
</style>
